@import './../../style/mixins';
@import './../../style/extends';

.return {
  grid-column: span 2;

  @include on-tablet {
    grid-column: span 6;
  }

  @include on-tablet {
    grid-column: span 24;
  }

  &__title {
    @extend %h1;

    margin-top: 20px;

    @include on-tablet {
      margin-top: 30px;
    }

    @include on-tablet {
      margin-top: 50px;
    }
  }

  &__title-h2 {
    @extend %h2;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 40px;
    margin: 20px;

    @include on-tablet {
      margin: 30px;
      gap: 50px;
    }

    @include on-tablet {
      margin-block: 50px;
    }
  }

  &__article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;

    @include on-tablet {
      gap: 20px;
    }
  }

  &__text {
    @extend %body-text;
    
    max-width: 800px;
  }

  &__text-h3 {
    @extend %h3;

    margin-block: 20px;
  }


  &__list {
    display: flex;
    flex-direction: column;
    gap: 8px;
    list-style-type: disc;
  }

  &__list-item {
    @extend %body-text;
  }

  &__bold {
    @extend %body-text;

    font-family: Mont-Bold, $fallback-fonts;
  }

  &__small {
    @extend %small-text;

    max-width: 80vw;
  }
}
